<template>
<div class="grid">
    <tbody>
      <tr class="row" v-for="(row, rInd) in grid" :key="`row-${rInd}`">
        <td
          class="cell"
          v-for="(cell, cInd) in row"
          :class="cell === 0 ? 'dead' : 'alive'"
          :key="`cell-${rInd}${cInd}`"
           @click="changeCellValue(rInd, cInd)">
        </td>
      </tr>
    </tbody>
</div>
</template>

<script>
export default {
  name: 'grid',
  computed: {
    grid () {
      return this.$store.getters.getGrid
    }
  },
  methods: {
    changeCellValue (x, y) {
      this.$store.dispatch('changeCell', {x: x, y: y})
    }
  }
}
</script>

<style scoped lang="sass">
$cell-border: 1px solid grey
$font-size: 8px
$cell-size: 12px
.grid
  border: $cell-border
  display: inline-block

.cell
  border: $cell-border
  font-size: $font-size
  width: $cell-size
  height: $cell-size
  cursor: pointer

.alive
  background: #42b983

.dead
  background: #272822
</style>



